<template>
  <v-row justify="center"
         @click.stop="showAroma = !showAroma"
         class="ma-0 pb-3"
         style="min-height: 47px;">
    <v-icon
        v-for="(e, i) in arrayToShow" :key="i"
        large
        :class="stylesToShow[i]"
    >
      mdi-circle
    </v-icon>
  </v-row>
</template>

<script>
export default {
  name: 'ColorList',
  props: {
    aroma: Array,
    taste: Array,
    sensoryPerception: Boolean
  },
  data: () => ({
    showAroma: true,
    aromaStyles: ['grassy', 'onion-like', 'flowery', 'balsamic',
      'heavy-floral', 'aromatic', 'spicy', 'roasty', 'trigeminus'],
    tasteStyles: ['sweet', 'sour', 'salty', 'bitter', 'umami', 'trigeminus']
  }),
  computed: {
    perceptionToShow () {
      return this.sensoryPerception !== undefined ? this.sensoryPerception : this.showAroma
    },
    arrayToShow () {
      return this.perceptionToShow ? this.aroma : this.taste
    },
    stylesToShow () {
      return this.perceptionToShow ? this.aromaStylesToShow : this.tasteStylesToShow
    },
    aromaStylesToShow () {
      if (this.$vuetify.breakpoint.xsOnly) {
        return this.aromaStyles.map((e, i) => (
            this.aroma[i] === 1
                ? e.concat(' xs-big-icon')
                : e.concat(' xs-small-icon')
        ))
      } else {
        return this.aromaStyles.map((e, i) => (
            this.aroma[i] === 1
                ? e.concat(' big-icon')
                : e.concat(' small-icon')
        ))
      }
    },
    tasteStylesToShow () {
      if (this.$vuetify.breakpoint.xsOnly) {
        return this.tasteStyles.map((e, i) => (
            this.taste[i] === 2
                ? e.concat(' xs-big-icon')
                : this.taste[i] === 1
                ? e.concat(' xs-middle-icon')
                : e.concat(' xs-small-icon')
        ))
      } else {
        return this.tasteStyles.map((e, i) => (
            this.taste[i] === 2
                ? e.concat(' big-icon')
                : this.taste[i] === 1
                ? e.concat(' middle-icon')
                : e.concat(' small-icon')
        ))
      }
    }
  }
}
</script>

<style scoped>
.xs-small-icon {
  font-size: 7px!important;
}
.xs-middle-icon {
  font-size: 13px!important;
}
.xs-big-icon {
  font-size: 22px!important;
}
.small-icon {
  font-size: 10px!important;
}
.middle-icon {
  font-size: 24px!important;
}
.big-icon {
  font-size: 29px!important;
}
</style>
