<template>
  <div>
    <TopToolbarSelection :backwardRoute="'Home'"/>
    <v-container class="pa-0 fill-height" style="justify-content: center">
      <v-item-group
          multiple
          class="fill-height w-100"
      >
        <p class="title text-uppercase text-center">
          Zutaten Automat
        </p>
        <v-card outlined
                style="height: 80%"
                class="my-card">
          <v-card-text class="pa-0" style="height: 83%">
            <v-row no-gutters class="fill-height">
              <v-col v-for="n in 3"
                     :key="n"
                     cols="12"
                     sm="4"
                     class="pa-0"
              >
                <v-item>
                  <v-card tile outlined
                          :style="$vuetify.breakpoint.xsOnly && n !== 3
                              ? 'height: 100%; border-top: 0px; border-left: 0px; border-right: 0px;'
                              : n === 3
                                  ? 'height: 100%; border-top: 0px; border-left: 0px; border-right: 0px; ' +
                                   'border-bottom: 0px;'
                                  : 'height: 100%; border-top: 0px; border-left: 0px; ' +
                                   'border-bottom: 0px;'"
                          :to="{name: 'IngredientSelector',
                                 params: {
                                     cardIndex: n,
                                     fusionHistoryProp: fusionHistory
                               }}">
                    <v-card-title v-if="fusionIngredients[n-1].name" class="fill-height pa-6">
                      <v-row justify-sm="center">
                        <v-col :cols="$vuetify.breakpoint.xsOnly ? '10' : '12'"
                               :class="$vuetify.breakpoint.xsOnly ? '' : 'text-center'">
                          <v-avatar
                              :class="$vuetify.breakpoint.xsOnly
                              ? 'mr-3' : ''"
                              color="rgb(210, 210, 249)"
                              :size="$vuetify.breakpoint.xsOnly
                              ? '30' : '56'"
                          ></v-avatar>
                          <span v-if="fusionIngredients[n-1]" class="text-uppercase title black--text hidden-sm-and-up">
                            {{fusionHistory[fusionIndex][n-1].name}}
                          </span>
                          <p v-if="fusionIngredients[n-1]" class="text-uppercase title black--text hidden-xs-only mt-5">
                            {{fusionHistory[fusionIndex][n-1].name}}
                          </p>
                        </v-col>
                        <v-col :cols="$vuetify.breakpoint.xsOnly ? '2' : '12'"
                               :class="$vuetify.breakpoint.xsOnly ? '' : 'text-center'">
                          <v-btn icon
                                 :input-value="fusionHistory[fusionIndex][n-1].active"
                                 @click.prevent="fusionHistory[fusionIndex][n-1].active = !fusionHistory[fusionIndex][n-1].active">
                            <v-icon>
                              {{ fusionHistory[fusionIndex][n-1].active ? 'mdi-lock' : 'mdi-lock-open-outline' }}
                            </v-icon>
                          </v-btn>
                        </v-col>
                      </v-row>
                    </v-card-title>
                    <v-card-title class="fill-height sheet-color justify-center" v-else>
                      <v-row>
                        <v-col cols="12" class="pa-0 text-center">
                          <span class="body-1 text-uppercase mx-auto grey--text">Klicken</span>
                        </v-col>
                        <v-col cols="12" class="pa-0 text-center">
                          <span class="body-2 text-uppercase mx-auto grey--text">um Zutat auswählen</span>
                        </v-col>
                      </v-row>
                    </v-card-title>
                  </v-card>
                </v-item>
              </v-col>
            </v-row>
          </v-card-text>
          <v-card-actions style="height: 17%; background: rgb(247, 247, 247) none repeat scroll 0 0; border-top: 1px solid black">
            <v-btn
                icon
                color="rgba(28, 28, 225, 0.8) !important"
                :disabled="!canUndo"
                @click="fusionIndex--"
            >
              <v-icon>mdi-undo</v-icon>
            </v-btn>
            <v-btn
                icon
                color="rgba(28, 28, 225, 0.8) !important"
                :disabled="!canRedo"
                @click="fusionIndex++"
            >
              <v-icon>mdi-redo</v-icon>
            </v-btn>
            <v-btn class="text-uppercase ml-auto"
                   outlined
                   @click="generateAF"
            >generieren</v-btn>
          </v-card-actions>
        </v-card>
        <BottomNavigationSelector
            :hide-senses="true"
            :selectionSliders.sync="selectionSliders">
        </BottomNavigationSelector>
        <v-row justify="center">
          <v-btn class="text-uppercase app-primary"
                 fixed bottom
                 outlined
                 :disabled="fusionIndex === 0"
                 @click="sheet = !sheet"
          >fusionieren</v-btn>
        </v-row>
      </v-item-group>
    </v-container>
    <div class="text-center rounded-t-xl">
      <v-bottom-sheet
          v-model="sheet"
          inset
          class="rounded-t-xl">
        <v-sheet
            class="text-center rounded-t-xl"
            height="500px">
          <v-row
              class="rounded-t-xl"
              no-gutters>
            <v-btn
                class="mt-6 ml-auto"
                text
                @click="sheet = !sheet">
              <v-icon dark>mdi-close</v-icon>
            </v-btn>
          </v-row>
          <v-container
              class="pa-0"
              style="height: 350px"
              no-gutters @click="showAromaFusion = !showAromaFusion, key++">
            <base-polar-area :key="key"
                             :aromaFusion="[...this.fusionHistory[this.fusionIndex]]"
                             :showAromaFusion="showAromaFusion"
                             style="height: 300px; position: relative"/>
            <v-row no-gutters class="mt-0 pt-3" align="center">
              <v-col cols="7"></v-col>
              <v-col cols="2" align="end">
                <v-icon small class="pl-13">
                  mdi-information-outline
                </v-icon>
              </v-col>
              <v-col class="caption" cols="2">
                Tippen für {{ showAromaFusion ? 'Geschmack' : 'Aroma' }}
              </v-col>
            </v-row>
            <v-row justify="center">
              <v-btn class="text-uppercase mx-auto"
                     fixed bottom
                     outlined
                     color="rgba(28, 28, 225, 0.8) !important"
                     :to="{
                           name: 'PreparationSelector',
                           params: {
                             fusionIngredients: fusionIngredients,
                             fusionHistoryProp: fusionHistory,
                             fusionIndex: fusionIndex
                           }}"
              >Nehm ich!</v-btn>
            </v-row>
          </v-container>
        </v-sheet>
      </v-bottom-sheet>
    </div>
  </div>
</template>

<script>
import TopToolbarSelection from '@/components/TopToolbarSelection'
import BottomNavigationSelector from '@/components/BottomNavigationSelector'
import BasePolarArea from '@/components/BasePolarArea'
import ingredients from '../../public/data/ingredients.json'

export default {
  name: "FlavourMerger.vue",
  components: {
    TopToolbarSelection,
    BottomNavigationSelector,
    BasePolarArea
  },
  props: {
    fusionHistoryProp: Array
  },
  data: () => ({
    selectionSliders: {
      showAroma: true,
      showHarmony: true
    },
    ingredientList: ingredients,
    fusionHistory: [[{}, {}, {}]],
    fusionIndex: 0,
    sheet: false,
    showAromaFusion: true,
    key: 0
  }),
  computed: {
    fusionIngredients () {
      return [...this.fusionHistory[this.fusionIndex]]
    },
    canUndo () {
      return this.fusionHistory.length && this.fusionIndex !== 0
    },
    canRedo () {
      return this.fusionHistory.length
          && this.fusionIndex !== this.fusionHistory.length - 1
    }
  },
  created() {
    if (this.fusionHistoryProp) {
      this.fusionHistory = this.fusionHistoryProp
      this.fusionIndex = this.fusionHistoryProp.length -1
    } else {
      console.log('No fusion history')
    }
  },
  methods: {
    generateAF () {
      const fusionIngredientsUnlocked = !this.fusionIngredients.some(e => e.active)
          ? [...this.ingredientList]
          : this.ingredientList.filter(e =>
              !this.fusionIngredients.some(f => e.id === f.id && f.active ))

      const shuffledfusionIngredientsUnlocked = this.fisherYatesShuffle(fusionIngredientsUnlocked)

      // TODO Forbid the same ingredient at the same position if unlocked
      const newFusion = this.fusionIngredients.map((e,i) =>
          (e.active ? e : {...shuffledfusionIngredientsUnlocked[i], 'active': false})
      )

      this.fusionHistory.push(newFusion)
      this.fusionIndex = this.fusionHistory.length - 1
    },

    fisherYatesShuffle(array) {
      // Fisher Yates Shuffle from https://medium.com/@omar.rashid2/fisher-yates-shuffle-a2aa15578d2f
      let oldElement
      for (let i = array.length - 1; i > 0; i--) {
        let rand = Math.floor(Math.random() * (i + 1));
        oldElement = {...array[i]};
        array[i] = {...array[rand]};
        array[rand] = oldElement;
      }
      return array
    }
  }
}
</script>

