<script>
import { PolarArea } from 'vue-chartjs'

export default {
  extends: PolarArea,
  name: "BasePolarArea",
  props: {
    aromaFusion: Array,
    showAromaFusion: Boolean,
    tasteArray: Array,
    aromaArray: Array
  },
  data: () => ({
    options: {
      responsive: true,
      maintainAspectRatio: false,
      legend: {
        position: 'bottom',
        labels: {
          usePointStyle: true,
          padding: 18
        },
      },
      scale: {
        ticks: {
          stepSize: 1
        }
      },
      plugins: {
        datalabels: {
          formatter: (value, ctx) => {
            const label = ctx.chart.data.labels[ctx.dataIndex];
            if (ctx.dataset.data[ctx.dataIndex] > 0)
              return label + " : " + ctx.dataset.data[ctx.dataIndex];
            else
              return "" // retun empty if the data for label is empty
          },
        }
      },
    }
  }),

  computed: {
    labelsComputed () {
      return this.showAromaFusion
          ? ['Grün', 'Schwefelig', 'Zitrusartig', 'Balsamisch', 'Dunkel','Aromatisch', 'Würzig', 'Röstaromen']
          : ['Süß', 'Sauer', 'Salzig', 'Bitter', 'Umami', 'Trigeminus']
    },
    backgroundColorComputed () {
      return this.showAromaFusion
          ? ['rgba(129, 212, 250, 0.5)',
            'rgba(159, 168, 218, 0.5)',
            'rgba(206, 147, 216, 0.5)',
            'rgba(248, 187, 208, 0.5)',
            'rgba(240, 98, 146, 0.5)',
            'rgba(255, 204, 128, 0.5)',
            'rgba(230, 238, 156, 0.5)',
            'rgba(197, 225, 165, 0.5)',
            'rgba(229, 115, 115, 0.5)'
          ]
          : ['rgba(244, 143, 177, 0.5)',
            'rgba(165, 214, 167, 0.5)',
            'rgba(128, 203, 196, 0.5)',
            'rgba(179, 157, 219, 0.5)',
            'rgba(239, 154, 154, 0.5)',
            'rgba(229, 115, 115, 0.5)'
          ]
    },
    borderColorComputed () {
      return this.showAromaFusion
          ? ['rgba(129, 212, 250, 1)',
            'rgba(159, 168, 218, 1)',
            'rgba(206, 147, 216, 1)',
            'rgba(248, 187, 208, 1)',
            'rgba(240, 98, 146, 1)',
            'rgba(255, 204, 128, 1)',
            'rgba(230, 238, 156, 1)',
            'rgba(197, 225, 165, 1)',
            'rgba(229, 115, 115, 1)'
          ]
          : ['rgba(244, 143, 177, 1)',
            'rgba(165, 214, 167, 1)',
            'rgba(128, 203, 196, 1)',
            'rgba(179, 157, 219, 1)',
            'rgba(239, 154, 154, 1)',
            'rgba(229, 115, 115, 1)'
          ]
    },
    dataComputed () {
      return this.showAromaFusion
          ? this.fusionPolarData.fusionAroma
          : this.fusionPolarData.fusionTaste
    },
    fusionPolarData () {
      return {
        fusionAroma: this.aromaArray ? this.aromaArray : this.sumFusion('aroma'),
        fusionTaste: this.tasteArray ? this.tasteArray : this.sumFusion('taste')
      }
    }
  },

  mounted () {
    this.renderChart({
      labels: this.labelsComputed,
      datasets: [{
        data: this.dataComputed,
        backgroundColor: this.backgroundColorComputed,
        borderColor: this.borderColorComputed,
        borderWidth: 1
      }]
    }, this.options)
  },

  methods: {
    sumFusion (property) {
      this.key++
      const arrayToSum = this.aromaFusion
          .filter(ingredient => ingredient[property] !== undefined)
          .map(ingredient => (ingredient[property]))
      return arrayToSum.length > 1
          ? arrayToSum.reduce((r, a) => a.map((b, i) => (r[i] || 0) + b), [])
          : arrayToSum[0]
    }
  }
}
</script>

