<template>
  <v-row class="mx-2">
    <v-col cols="6"
           v-if="!hideSenses">
      <v-switch
          class="switch-color"
          :input-value="true"
          inset
          :label="aromaLabel"
          :value="selectionSliders.showAroma"
          @change="$emit('update:selectionSliders',
              {...selectionSliders, 'showAroma': !selectionSliders.showAroma});"
      />
    </v-col>
    <v-spacer
        v-if="!hideSenses"/>
    <v-col :class="hideSenses ? 'my-12-col' : 'my-6-col'" class="my-auto">
      <v-switch
          :input-value="true"
          class="v-input--reverse switch-color"
          inset
          :disabled="selectionSlidersSelectedItemsEmpty"
          :value="selectionSliders.showHarmony"
          @change="$emit('update:selectionSliders',
              {...selectionSliders, 'showHarmony': !selectionSliders.showHarmony});"
      >
        <template class="text-right" #label>
          <p class="text-right ma-0 pr-2" style="width: 100%">
            {{ harmonyLabel }}
          </p>
        </template>
      </v-switch>
    </v-col>
  </v-row>
</template>

<script>

export default {
  name: 'BottomNavigationSelector',
  props: {
    selectionSlidersSelectedItemsEmpty: Boolean,
    hideSenses: Boolean,
    selectionSliders: Object
  },
  computed: {
    aromaLabel: function () {
      return this.selectionSliders.showAroma ? 'AROMA' : 'GESCHMACKS'
    },
    harmonyLabel: function () {
      return this.selectionSliders.showHarmony ? 'HARMONIE' : 'KONTRAST'
    },
  }
}
</script>

