.v-input--selection-controls {
  margin-top: 8px!important;
}
.v-label.theme--light {
  color: black
}
.theme--light.v-label--is-disabled {
  color: rgba(0, 0, 0, 0.38);
}
.my-6-col {
  flex: 0 0 50%;
  max-width: 50%;
}
.my-12-col {
  flex: 0 0 100%;
  max-width: 100%;
}

.switch-color .v-input--selection-controls__input div {
  color: rgba(28, 28, 225, 0.8)!important;
}

