                         :fusionHistoryProp="fusionHistoryProp">
      <template v-slot:additionalNavigation>
        <BaseCancelDialog/>
      </template>
    </TopToolbarSelection>
    <TopToolbarSelection v-else
                         :backwardRoute="'Home'">
      <template v-slot:additionalNavigation>
        <BaseCancelDialog :deleteDialog="true"/>
        <v-btn icon :color="!editExistingAF ? 'black' : 'rgba(28, 28, 225, 0.8)'" @click.stop="editExistingAF = !editExistingAF">
          <v-icon>mdi-square-edit-outline</v-icon>
        </v-btn>
      </template>
    </TopToolbarSelection>
    <v-container>
      <v-row no-gutters justify="end" class="py-2">
        <v-col
            cols="12"
            sm="8"
            md="8"
        >
          <v-text-field
              v-if="editMode || editExistingAF"
              color="rgba(28, 28, 225, 0.8)"
              v-model="aromaFusionEdit.name"
              outlined
              :class="$vuetify.breakpoint.xsOnly
                  ? 'title' : 'headline'"
              id="name-af"
              hide-details="auto"
          ></v-text-field>
          <p v-else
             :class="$vuetify.breakpoint.xsOnly
                  ? 'title text-uppercase mx-3 text-right' : 'headline text-uppercase mx-3 text-right'"
             style="word-break: break-word"
          >
            {{ aromaFusionEdit.name }}
          </p>
        </v-col>
      </v-row>


      <v-row no-gutters class="py-2">
        <v-col
            cols="12"
            sm="4"
            md="4"
            align-self="center"
        >
          <p :class="$vuetify.breakpoint.xsOnly
               ? 'subtitle-1 mb-0 font-weight-medium ml-1 pa-2'
               : 'subtitle-1 mb-0 font-weight-medium ml-1 pa-2 text-right'">
            Zutaten :
          </p>
        </v-col>
        <v-col
            cols="12"
            sm="8"
            md="8"
            :align="$vuetify.breakpoint.xsOnly ? 'center' : 'left'"
        >
          <v-avatar
              class="mx-3"
              color="rgb(210, 210, 249)"
              size="56"
          ></v-avatar>
          <v-avatar
              class="mx-3"
              color="rgb(210, 210, 249)"
              size="56"
          ></v-avatar>
          <v-avatar
              class="mx-3"
              color="rgb(210, 210, 249)"
              size="56"
          ></v-avatar>
        </v-col>
      </v-row>

      <v-row no-gutters class="py-2">
        <v-col
            cols="12"
            sm="4"
            md="4"
            align-self="center"
        >
          <p :class="$vuetify.breakpoint.xsOnly
               ? 'subtitle-1 mb-0 font-weight-medium ml-1 pa-2'
               : 'subtitle-1 mb-0 font-weight-medium ml-1 pa-2 text-right'">
            Zubereitungsart :
          </p>
        </v-col>
        <v-col
            cols="12"
            sm="8"
            md="8"
            :align="$vuetify.breakpoint.xsOnly ? 'center' : 'left'"
        >
          <v-avatar
              :class="$vuetify.breakpoint.xsOnly ? 'ml-auto mr-3' : 'mx-3'"
              color="rgb(210, 210, 249)"
              size="56"
          ></v-avatar>
          <span class="subtitle-1 font-weight-medium pa-2 w-100 ma-0" style="width: 70%">
            {{ aromaFusionEdit.preparationType }}
          </span>
