      </v-row>

      <v-row no-gutters class="py-2">
        <v-col
            cols="12"
            sm="4"
            md="4"
            class="mt-2"
        >
          <p :class="$vuetify.breakpoint.xsOnly
               ? 'subtitle-1 mb-0 font-weight-medium ml-1 pa-2'
               : 'subtitle-1 mb-0 font-weight-medium ml-1 pa-2 text-right'">
            Aroma Fusion :
          </p>
        </v-col>
        <v-col @click="showAromaFusion = !showAromaFusion, key++"
               v-if="aromaFusionEdit && aromaFusionEdit.ingredients"
               cols="12"
               sm="8"
               md="8"
               align="center"
        >
          <base-polar-area
                           :key="key"
                           :aromaFusion="aromaFusionEdit.ingredients"
                           :tasteArray="aromaFusionEdit.taste"
                           :aromaArray="aromaFusionEdit.aroma"
                           :showAromaFusion="showAromaFusion"
                           style="height: 300px; position: relative"/>
        </v-col>
      </v-row>

      <v-row no-gutters class="py-2">
