        >
          <v-textarea
              v-if="editMode || editExistingAF"
              color="rgba(28, 28, 225, 0.8)"
              v-model="aromaFusionEdit.notes"
              outlined
              hide-details="auto"
              auto-grow
          />
          <p v-else
             class="subtitle-1 mb-0 font-weight-medium ml-1 pa-2 mt-2"
          >
            {{ aromaFusionEdit.notes }}
          </p>
        </v-col>
      </v-row>
    </v-container>
    <v-btn v-if="editMode || editExistingAF" class="mx-2 app-primary" fab small fixed right bottom
           @click="saveAFRouteHome">
      <v-icon color="white">mdi-check</v-icon>
    </v-btn>
  </v-container>
</template>

<script>
import TopToolbarSelection from '@/components/TopToolbarSelection'
import BaseCancelDialog from '@/components/BaseCancelDialog'
import BasePolarArea from '@/components/BasePolarArea'
import localStorageMixin from '@/mixins/localStorageMixin'
import router from "../router"

export default {
  name: 'AromaFusion.vue',
  components: {
    TopToolbarSelection,
    BaseCancelDialog,
    BasePolarArea
  },
  mixins: [localStorageMixin],
  props: {
    preparationType: Object,
    fusionHistoryProp: Array,
    editMode: Boolean,
    aromaFusion: Object,
    fusionIndex: Number
  },
  data: () => ({
    aromaFusionEdit: {},
    editExistingAF: false,
    key: 0,
    showAromaFusion: true
  }),
  mounted() {
    if (this.editMode) {
      this.aromaFusionEdit = {
        name: this.getIngredientName(0) + this.getIngredientName(1) + this.getIngredientName(2),
        ingredients: this.fusionHistoryProp[this.fusionHistoryProp.length -1],
        ingredientString: this.getIngredientName(0) + this.getIngredientName(1)
            + this.getIngredientName(2),
        preparationType: this.preparationType.name,
        aroma: [0, 1, 1, 0, 0, 0, 0, 1, 1],
        taste: [2, 0, 1, 0, 0, 1],
      }
    } else {
      this.aromaFusionEdit = {...this.aromaFusion,
        // eslint-disable-next-line no-prototype-builtins
        notes: (this.aromaFusion.hasOwnProperty('notes')) ? this.aromaFusion.notes : ''}
    }
  },
  methods: {
    getIngredientName(index) {
      return this.fusionHistoryProp[this.fusionIndex][index].name ?
          this.fusionHistoryProp[this.fusionIndex][index].name + ' ' : ''
    },
    saveAFRouteHome() {
      this.addAF(this.aromaFusionEdit)
      router.push({ name: 'Home'})
    }
  }
}
</script>

<style>
#name-af {
  text-align: right !important;
  text-transform: uppercase !important;
}
.v-input--is-focused .v-input__slot {
  box-shadow: rgb(210, 210, 249) 4px 4px 0px 0px;
}

