<template>
  <v-container class="px-0" id="scrolling-target">
    <v-item-group
        multiple
        max="3"
    >
      <div class="masonry">
        <v-item v-slot="{ active, toggle }" v-for="(item, i) in itemList"
                :key="i + item.name">
          <v-card @click="clickCard(i)"
                  outlined class="card-brick my-card">
            <v-row justify="center">
              <v-card-title
                  style="width: 100%"
                  :class="$vuetify.breakpoint.xsOnly
                  ? 'title text-uppercase mx-3' : 'headline text-uppercase mx-3'"
              >
                {{ item.name }}
              </v-card-title>
            </v-row>
            <div class="d-flex flex-no-wrap justify-space-between">
              <v-card-subtitle v-if="item.ingredients"
                               style="color: rgba(0,0,0,0.6)"
                               :class="$vuetify.breakpoint.xsOnly
                                 ? '' : 'body-1'"
              >
                {{item.ingredientString}} <br/>
                {{item.preparationType}}
              </v-card-subtitle>
              <v-card-subtitle v-if="item.tasteDescription || item.description"
                               style="color: rgba(0,0,0,0.6)">
                {{item.tasteDescription}}
                {{item.description}}
              </v-card-subtitle>
            </div>
            <ColorList v-if="item.taste && item.aroma"
                       :taste="item.taste"
                       :aroma="item.aroma"
                       :sensoryPerception="selectionSliders ? selectionSliders.showAroma : null"/>
          </v-card>
        </v-item>
      </div>
    </v-item-group>
  </v-container>
</template>

<script>
import ColorList from '@/components/ColorList';

export default {
  name: 'CardList.vue',
  props: {
    itemList: {
      type: Array,
      default: function () {return []}
    },
    selectionSliders: {
      type: Object,
      default: function () {return {}}
    },
    cardIndex: {
      type: Number,
      default: NaN
    },
    fusionHistoryProp: {
      type: Array,
      default: function () {return []}
    },
    editMode: {
      type: Boolean,
      default: false
    },
    fusionIndex: {
      type:Number,
      default: NaN
    }
  },
  components: {
    ColorList
  },
  methods: {
    clickCard (index) {
      if (this.cardIndex) {
        let lastElement = [...this.fusionHistoryProp[this.fusionHistoryProp.length-1]]
        lastElement.splice(this.cardIndex -1, 1,
            {...this.itemList[index], active: false})
        this.$router.push({
          name: 'FlavourMerger',
          params: {
            cardIndex: this.cardIndex,
            fusionHistoryProp: this.fusionHistoryProp.concat([lastElement]),
            fusionIndex: this.fusionIndex
          }
        })
      } else {
        this.$router.push({
          name: 'AromaFusion',
          params: {
            preparationType: this.itemList[index],
            fusionHistoryProp: this.fusionHistoryProp,
            editMode: this.editMode,
            aromaFusion: this.itemList[index],
            fusionIndex: this.fusionIndex
          }
        })
      }
    }
  }
}
</script>

