/*masonry layout by Franz Vezuli from https://github.com/vuetifyjs/vuetify/issues/4091 */

.masonry {
  margin: 0 auto 0 auto;
  column-gap: 1.3em;
  padding: 2px 5px 0px 5px;
}

@media only screen and (min-width: 600px) {
  .masonry {
    column-count: 2;
  }
}

.card-brick {
  display: inline-block;
  margin: 0 auto 1em auto!important;
  width: 100%;
}

</style>
