export default {
    data: () => ({
        aromaFusions: []
    }),
    mounted() {
        if (localStorage.getItem('aromaFusions')) {
            try {
                this.aromaFusions = JSON.parse(localStorage.getItem('aromaFusions'))
            } catch(e) {
                localStorage.removeItem('aromaFusions')
            }
        }
    },
    methods: {
        addAF(newAF) {
            if (!newAF) {
                return
            }

            this.aromaFusions.push(newAF)
            this.saveAF()
        },
        removeByIdAF(idAF) {
            this.aromaFusions = this.aromaFusions.filter(AF => AF.id !== idAF)
            this.saveAF()
        },
        removeByIndexAF(indexAF) {
            this.aromaFusions.splice(indexAF, 1)
            this.saveAF()
        },
        editByIdAF(idAF, newAF) {
            this.aromaFusions.map(AF => AF.id === idAF ? newAF : AF)
            this.saveAF()
        },
        editByIndexAF(indexAF, newAF) {
            this.aromaFusions[indexAF] = newAF
            this.saveAF()
        },
        saveAF() {
            localStorage.setItem('aromaFusions', JSON.stringify(this.aromaFusions))
        }
    }
};
