import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import FlavourMerger from '../views/FlavourMerger'
import IngredientSelector from '../views/IngredientSelector'
import PreparationSelector from '../views/PreparationSelector'
import BaseReturnDialog from '../views/AromaFusion'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/flavour-merger',
    name: 'FlavourMerger',
    component: FlavourMerger,
    props: true
  },
  {
    path: '/ingredients',
    name: 'IngredientSelector',
    component: IngredientSelector,
    props: true
  },
  {
    path: '/preparation',
    name: 'PreparationSelector',
    component: PreparationSelector,
    props: true
  },
  {
    path: '/aroma-fusion',
    name: 'AromaFusion',
    component: BaseReturnDialog,
    props: true
  }
]

const router = new VueRouter({
  routes
})

export default router
